.header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 8rem 1fr;
  margin-bottom: 12rem;
}
.header__information {
  max-width: 50rem;
  text-align: left;
  margin: 0 auto;
  grid-column: 1/2;
  grid-row: 1/3;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  padding: 20rem 3rem 0 3rem;
  position: relative;
}
.header__information .blurred-ball--green {
  position: absolute;
  top: -20%;
  left: 60%;
}
.header__information .blurred-ball--blue {
  position: absolute;
  top: -10%;
  left: -10%;
}
.header__information__title span {
  color: var(--primary);
}
.header__information__partnershipsContainer {
  display: flex;
  gap: 2rem;
}
.header__background-container {
  grid-column: 2/3;
  grid-row: 1/3;
  overflow: hidden;
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr;
}
.header__background-container__bgImage {
  grid-column: 1/2;
  grid-row: 1/3;
  height: 72rem;
  border-radius: 0 0 0 8rem;
  background-image: linear-gradient(18deg, var(--gradient) 0%, transparent 30%, transparent 100%), url(/images/header/header.png);
  width: 100%;
  text-align: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header__background-container__cards {
  grid-column: 1/2;
  grid-row: 2/3;
  display: flex;
  gap: 1.6rem;
  padding: 0 0 4.8rem 4.8rem;
}

.pollyfill {
  display: none;
}

.navbar {
  grid-row: 1/2;
  grid-column: 1/3;
}

/*
0 - 600px   Phone
600 - 900px     Tablet portrait
900 - 1200px    Tablet landscape
1200 - 1800px   Normal styles applied
1800px+    Big Desktop
*/
.navbar {
  width: 100%;
  color: #000;
  opacity: 0.9;
  height: 8rem;
  z-index: 12;
}
.navbar input[type=checkbox],
.navbar .hamburger__lines {
  display: none;
}
.navbar__container {
  display: flex;
  justify-content: space-between;
  padding: 2rem 12rem;
  align-items: center;
}
.navbar__container .menu__items {
  order: 2;
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.navbar__container .menu__items li {
  list-style: none;
}
.navbar__container .menu__items a:hover {
  transition: color 0.3s ease-in-out;
}
.navbar__container .logo {
  order: 1;
  margin-bottom: 0.5rem;
}
@media (max-width: 52.25em) {
  .navbar {
    position: fixed;
    opacity: 0.95;
  }
  .navbar input[type=checkbox],
  .navbar .hamburger__lines {
    display: block;
  }
  .navbar__container {
    display: block;
    position: relative;
    height: 64px;
    padding: 0;
  }
  .navbar__container input[type=checkbox] {
    display: block;
    position: absolute;
    height: 3rem;
    width: 3rem;
    top: 2rem;
    left: 2rem;
    z-index: 5;
    opacity: 0;
  }
  .navbar__container .hamburger__lines {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 3rem;
    width: 3.5rem;
    position: absolute;
    top: 2.5rem;
    left: 2rem;
    z-index: 2;
  }
  .navbar__container .hamburger__lines .line {
    display: block;
    height: 3px;
    width: 100%;
    background: var(--black);
  }
  .navbar__container .hamburger__lines .line.line1 {
    transform-origin: 0% 0%;
    transition: transform 0.4s ease-in-out;
  }
  .navbar__container .hamburger__lines .line.line2 {
    transition: transform 0.2s ease-in-out;
  }
  .navbar__container .hamburger__lines .line.line3 {
    transform-origin: 0% 100%;
    transition: transform 0.4s ease-in-out;
  }
  .navbar .menu__items {
    padding-top: 100px;
    background: var(--white);
    height: 100vh;
    max-width: 30rem;
    transform: translate(-150%);
    flex-direction: column;
    margin-left: -40px;
    padding-left: 50px;
    box-shadow: 0.5rem 0px 2rem 0 rgba(226, 226, 226, 0.5882352941);
  }
  .navbar .menu__items li {
    margin-bottom: 1.5rem;
    font-size: 1.3rem;
    font-weight: 500;
  }
  .navbar .menu__items li .button {
    color: var(--black);
    background-color: transparent;
    border: 1px solid var(--black) !important;
  }
  .navbar .logo {
    position: absolute;
    top: 2.5rem;
    right: 2rem;
    font-size: 2rem;
  }
  .navbar input[type=checkbox]:checked ~ .menu__items {
    transform: translateX(0);
    transition: transform 0.5s ease-in-out;
  }
  .navbar input[type=checkbox]:checked ~ .hamburger__lines .line1 {
    transform: rotate(50deg);
  }
  .navbar input[type=checkbox]:checked ~ .hamburger__lines .line2 {
    transform: scaleY(0);
  }
  .navbar input[type=checkbox]:checked ~ .hamburger__lines .line3 {
    transform: rotate(-50deg);
  }
}

